/**
 * 
 * @param {FetchMock} fetchMock fetchMock
 * @param {function} delay  ms : delay(mockData)  delay(mockData, 200)
 * @param {function} mock mock:

   mock({
     'string|1-10': '' // 110
   })

   // {'string': ''} 

   http://mockjs.com/examples.html
 */
export default ({fetchMock, delay, mock, toSuccess, toError}) => {
  // fetchMock
  // fetchMock.mock(/httpbin.org\/post/, {/* response */}, {/* options */});

  return {
    // 
    'GET /api/getUserInfo': {
      name: '',
      sex: '',
      age: 18,
    },
    //  method, 
    '/api/getUsers': delay([
      { name: 'jonn' },
      { name: 'weiq' },
    ]),
    // , 
    // 
    '/api/userInfo/getList': (options) => {
      const body = JSON.parse(options.body);
      const pageNum = body.pageNum;
      const idbase = (pageNum - 1) * 10 + 1;
      return toSuccess(mock({
        'pageNum': pageNum,
        'pageSize': 10,
        'size': 10,
        'total': 100,
        'totalPages': 10,
        'list|10': [{
          'id|+1': idbase,
          'name': '@cname',                   // 
          'age|1-100': 100,                   // 100
          'birthday': '@date("yyyy-MM-dd")',  // 
          'city': '@city(true)',              // 
          'phone': /^1[385][1-9]\d{8}/        // 
        }],
      }), 400)
    },
    '/api/user/menu': options => toSuccess([
      {
        name: '',
        icon: 'home',
        path: '/machineRoom',
      },
      {
        name: '',
        icon: 'home',
        path: '/device',
      },
      {
        name: '',
        icon: 'home',
        path: '/calender',
      },
      {
        name: '',
        icon: 'home',
        path: '/contact',
      },
    ], 400)
  } 
}


